.container {
  width: 100%;
  max-width: 118rem;
  margin: 0 auto;
  padding: 0 2rem; }

.layout-root {
  width: 100%;
  padding: 0;
  font-size: 10px;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased; }

.base-grid-layout {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  grid-auto-flow: dense; }

.grid-element--full-width {
  grid-column-end: span 4; }

.grid-element--three-quarter {
  grid-column-end: span 3; }
  @media (max-width: 102rem) {
    .grid-element--three-quarter {
      grid-column-end: span 4; } }

.grid-element--one-half {
  grid-column-end: span 2; }
  @media (max-width: 42rem) {
    .grid-element--one-half {
      grid-column-end: span 4; } }

.grid-element--one-quarter {
  grid-column-end: span 1; }
  @media (max-width: 102rem) {
    .grid-element--one-quarter {
      grid-column-end: span 2; } }
  @media (max-width: 42rem) {
    .grid-element--one-quarter {
      grid-column-end: span 4; } }

:global(.page-leave) {
  opacity: 1;
  transition: all 0.4s ease-in-out; }

:global(.page-leave.page-leave-active) {
  opacity: 0;
  transition: opacity .4s ease-in; }

:global(.page-enter) {
  opacity: 0; }

:global(.page-enter.page-enter-active) {
  opacity: 1;
  /* Delay the enter animation until the leave completes */
  transition: opacity .4s ease-in .6s; }

:global(.page-height) {
  transition: height .6s ease-in-out; }

.header-area {
  background-color: #0090c1;
  color: #fff;
  position: relative; }
  .header-area:before {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%);
    width: 100%;
    left: 0;
    top: 0;
    height: 1.5rem;
    display: block;
    opacity: 0.2;
    content: ''; }

.header-top {
  display: flex;
  align-items: center;
  padding: 0.5rem 0 2rem; }

.header-logo-container img {
  height: 6rem; }



