.container {
  width: 100%;
  max-width: 118rem;
  margin: 0 auto;
  padding: 0 2rem; }

.layout-root {
  width: 100%;
  padding: 0;
  font-size: 10px;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased; }

.base-grid-layout {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  grid-auto-flow: dense; }

.grid-element--full-width {
  grid-column-end: span 4; }

.grid-element--three-quarter {
  grid-column-end: span 3; }
  @media (max-width: 102rem) {
    .grid-element--three-quarter {
      grid-column-end: span 4; } }

.grid-element--one-half {
  grid-column-end: span 2; }
  @media (max-width: 42rem) {
    .grid-element--one-half {
      grid-column-end: span 4; } }

.grid-element--one-quarter {
  grid-column-end: span 1; }
  @media (max-width: 102rem) {
    .grid-element--one-quarter {
      grid-column-end: span 2; } }
  @media (max-width: 42rem) {
    .grid-element--one-quarter {
      grid-column-end: span 4; } }

:global(.page-leave) {
  opacity: 1;
  transition: all 0.4s ease-in-out; }

:global(.page-leave.page-leave-active) {
  opacity: 0;
  transition: opacity .4s ease-in; }

:global(.page-enter) {
  opacity: 0; }

:global(.page-enter.page-enter-active) {
  opacity: 1;
  /* Delay the enter animation until the leave completes */
  transition: opacity .4s ease-in .6s; }

:global(.page-height) {
  transition: height .6s ease-in-out; }

.stat-bar-graph .stat-bar {
  fill: #0090c1; }

.stat-bar-graph :global(.recharts-cartesian-axis-tick) {
  font-size: 1rem;
  fill: #787280; }

.bar-tooltip {
  position: relative;
  left: -4rem;
  background-color: #2C2A2F;
  color: #fff;
  width: 6rem;
  border-radius: 0.5rem;
  padding: 1rem;
  text-align: center; }
  .bar-tooltip:after {
    top: 100%;
    left: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(136, 183, 213, 0);
    border-top-color: #2C2A2F;
    border-width: 1rem;
    margin-left: -1rem; }
  .bar-tooltip .tooltip-value {
    font-size: 1.4rem;
    font-weight: bold; }
  .bar-tooltip p {
    margin: 0;
    font-size: 1rem; }



