.bar-tooltip {
  position: relative;
  left: -6rem;
  background-color: #2C2A2F;
  color: #fff;
  width: 10rem;
  border-radius: 0.5rem;
  padding: 1rem;
  text-align: center; }
  .bar-tooltip:after {
    top: 100%;
    left: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-color: rgba(136, 183, 213, 0);
